*{
    box-sizing:border-box;
}

body{
    background-color: whitesmoke !important;
    font-family: sans-serif;
}


.navLink{
    color: rgb(209, 209, 209) ;
    text-decoration: none;
}

.active{
color: white;
}


.title{
    font-family: serif;
}



.gallery {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 2px;
    justify-content: center;
    transition: 0.3s;
  }

  

  .imgCon{
    margin: 10px;
    /* display: flex;
    flex-direction: row; */
      object-fit: cover;
      width: 500px;
      height: auto;
  }

  @media(max-width:620px){
    .imgCon{
      width:330px;
    }
  }

.gallery img{
    /* margin: 15px; */
    width: 100%;
    height: auto;
    transition: 0.4s ease-in-out;
}

.gallery img:hover{

    cursor: pointer;
    box-shadow: 1px 3px 5px rgba(0,0,0,0.4);
    transform: scale(1.05);
    transition: 0.4s ease-in-out;
}

